import Request from '@/utils/luch-request/index.js'

const getTokenStorage = () => {
	let token = ''
	try {
		token = uni.getStorageSync('token')
	} catch (e) {}
	return token
}

const http = new Request()

http.setConfig((config) => { /*  */
  config.baseURL = 'http://127.0.0.1:7001'
  config.header = {
    ...config.header,
    // a: 1, // 
    // b: 2 // 
  }
  return config
})

http.interceptors.request.use((config) => { /* async await  */
  config.header = {
    ...config.header,
    token: getTokenStorage()
  }
  /*
 if (!token) { // tokenreturn Promise.reject(config) 
   return Promise.reject(config)
 }
 */
  return config
}, (config) => {
  return Promise.reject(config)
})


http.interceptors.response.use(async (response) => { /* async await   */
  // if (response.data.code !== 200) { // 200reject()
  //   return Promise.reject(response)
  // }
  return response
}, (response) => { // async await 
  console.log(response)
  return Promise.reject(response)
})

export {
  http
}
